    color: #EEE;
    border: 1px solid #EEE;
    padding: 1.5%;
    margin-top: 2%;
    width: 35%;
    border-radius: 5px;
    outline: none;
}
.about .ourMessage .blogCard .blogButton:hover{
    background-color: #EEE;
    color: #563D7C;
    border: 1px solid #563D7C;
}

