.slider{
    height: 400px;
    position: relative;
    overflow: hidden;
}

.slider .sliderWrapper{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    position: relative;
}

.slider .sliderWrapper .slid{
    z-index: 1;
    width: 100%;
    height: 100%;
}

.slider .sliderWrapper .mainSlider{
    position:absolute;
    top:0;
    left:0;
    width:100%;
    height:100%;
    display:flex;
    align-items: center;
    justify-content: center;
}

.sliderImg{
    position:absolute;
    top:0;
    left:0;
    width:100vw;
    height:400px;
    object-fit: cover;
}

.sliderContent{
    position:absolute;
    z-index:10;
    display:flex;
    flex-direction:column;
    max-width:1600px;
    width:cala(100% - 100px);
    color: #FFF;
    left:0;
    margin-left: 3rem;
    background-color: rgba(0, 0, 0, 0.582);
    padding: 1%;
}

.sliderContent h1{
    font-size: clamp(1rem, 8vw, 2rem);
    font-weight: 400;
    text-transform: uppercase;
    text-shadow: 0px 0px 20px rgba(0,0,0,0.4);
    text-align:left;
    margin-bottom: .8rem;
    width: 80%;
}

.sliderContent p{
    margin-bottom:1.2rem;
    text-shadow: 0px 0px 20px rgba(0,0,0,0.4);
    width: 40%;;
}

