.CreateComment{
    margin: 2%;
    padding: 2%;
}
.CreateComment .buttonDiv{
    text-align: center;
}

.CreateComment .ReviewButton{
    outline: none;
    color: #FFF;
    padding: 1.5% 2.5%;
    border: none;
    background-color: #17A288;
    margin-right: 16.5%;
}

.CreateComment .ReviewButton:hover{
    background-color: #2ca08ba8;
}

.CreateComment .alert{
    margin-top: 4%;
    padding: 1%;
}

.CreateComment > h2{
    color: #FFF;
    margin: 1% 0 2%;
}

.CreateComment input{
    margin: 0 2% 1% 0;
    outline: none;
    border: none;
    width: 20%;
    height: 30px;
    padding: 1%;
    border-radius: 10px;
    background-color: #D3D3D3;
}

.CreateComment .span{
    font-size: 22px;
    color: #FFF;
    margin: 0 1.5% .5% 5%;
}

.CreateComment > h2.light,
.CreateComment .span.light{
    color: #000444;
}

.CreateComment textarea{
    float: left;
    width: 80%;
    margin-top: 2%;
    padding: 20px;
    background-color: #D3D3D3;
    border-radius: 10px;
    outline: none;
    height: 140px;
    resize: none;
}

.CreateComment .SendButton{
    float: left;
    outline: none;
    background-color: #EF442B;
    border-radius: 50%;
    padding: 1.7%;
    border: none;
    outline: none;
    margin: 9% 2% 0;
}

.CreateComment .SendButton:hover{
    background-color: #f05b44;
}

.CreateComment .SendButton .AddIcon{
    color: #FFF;
}

.CreateComment .Fab{
    margin: 8% 20px 0;
}

@media (max-width: 767px){  /* phone screen */
    
    .CreateComment input{
        width: 48%;
        height: 35px;
        margin-bottom: 5%;
    }
    
    .CreateComment .span{
        margin: 0 5% 0 0;
    }

    .CreateComment textarea{
        width: 100%;
        height: 170px;
    }
        
    .CreateComment .SendButton{
        border-radius: 5px;
        padding: 4% 10%;
        margin: 10% 35% 8%;
    }
}